header{
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    background-color: white;
    border-bottom: 1px solid rgb(194, 194, 194);
}

header h2{
    margin-left: 1rem;
    color: #FFA100;
    font-size: 30px;
    font-family: 'Courier New', Courier, monospace;
}

.auth button{
    margin: 1rem;
}
a{
    text-decoration: none;
}
